.twitch-container {
    width: 100vw;
    background: #9146ff; //#F8DC3D;
    background-position: bottom center;
    min-height: 90vh;
    height: 95vh;
    font-family: tahoma;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 0px;
    overflow: hidden;
    position: relative;
    z-index: 1;

    .twitch-logo {
        position: relative;
        left: 50%;
        top: -20px;
        width: auto;
        height: 60px;
        transform: translateX(-50%);

        @media (max-width: 992px) {
            top: 30px;
        }
    }

    @media (max-width: 992px) {
        height: 100vh;
    }

    &:before {
        content: "";
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background: rgba(0, 0, 0, 0.1);
        z-index: -1;
    }

    h4 {
        font-family: inherit;
        font-size: 40px;
        font-weight: bolder;
    }

    .padder {
        width: 100%;
        height: 65vh;
        margin-top: 30px;
        border: 5px solid white;
        box-shadow: 0px 2px 78px -11px rgba(0, 0, 0, 0.75);
        -webkit-box-shadow: 0px 2px 78px -11px rgba(0, 0, 0, 0.75);
        -moz-box-shadow: 0px 2px 78px -11px rgba(0, 0, 0, 0.75);

        @media (max-width: 992px) {
            min-height: 75vh;
        }

        iframe {
            flex: 1;
            height: 64vh;
            @media (max-width: 992px) {
                height: 74vh;
            }
        }
    }
}
